.portfolioWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0 100px 0;

  -webkit-box-shadow: inset 10px 200px 261px -84px #04040f;
  -moz-box-shadow: inset 10px 200px 261px -84px #04040f;
  box-shadow: inset 10px 200px 261px -84px #04040f;

  background: -moz-radial-gradient(circle at 50% 50%, #28081e 0%, #1e0d44 100%);
  background: -webkit-radial-gradient(
    circle at 50% 50%,
    #28081e 0%,
    #1e0d44 100%
  );
  background: -o-radial-gradient(circle at 50% 50%, #28081e 0%, #1e0d44 100%);
  background: -ms-radial-gradient(circle at 50% 50%, #28081e 0%, #1e0d44 100%);
  background: radial-gradient(circle at 50% 50%, #28081e 0%, #1e0d44 100%);
}

.sectionContainer {
  width: 90%;
  padding-top: 80px;
}
.sectionTitle {
  padding-bottom: 30px;
}
.imgContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.imgContainer img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}
.singleImgContainer {
  grid-template-columns: 1fr;
  place-items: center;
}
.singleImgContainer img {
  width: 800px;
  height: 400px;
  object-fit: cover;
}

/* ------------------------------------ media queries -------------------------------
------------------------------------------------------------------------------------- */
@media (max-width: 1400px) {
  .sectionContainer img {
    height: 300px;
  }
}
@media (max-width: 1260px) {
  .sectionContainer img {
    height: 300px;
  }
  .singleImgContainer img {
    height: 400px;
  }
}
@media (max-width: 1024px) {
  .imgContainer {
    grid-template-columns: 1fr;
  }
  .sectionContainer {
    width: 95%;
  }
  .sectionContainer img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }
}
@media (max-width: 768px) {
  .sectionContainer img {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }
}

@media (max-width: 460px) {
  .sectionContainer img {
    width: 100%;
    height: 230px;
    object-fit: cover;
  }
}
